<template>
  <div class="content_login">
    <div class="header">
      <img src="/logo.png" alt="">
    </div>
    <div class="body">
      <div>
        <input type="text" placeholder="手机号" v-model="value" @focus="isShow1.bool=false">
        <i v-show="value.length" class="iconfont" @click="value=''">&#xe64c;</i>
        <button @click="verifyNumber">{{ verifyStr }}</button>
        <div v-show="isShow1.bool">{{ isShow1.text }}</div>
      </div>
      <div>
        <input type="text" placeholder="验证码" v-model="yzvalue">
      </div>
    </div>
    <button :class="isBool?'footer_':'footer'" @click="login">登录</button>
  </div>

</template>

<script>
  import axios from "axios";
  import {Dialog} from "vant";

  export default {
    data() {
      return {
        isShow1: {
          bool: false,
          text: ""
        },
        value: '',
        isBool: false,
        verifyStr: "获取验证码",
        timer: [],
        yzvalue: ""
      }
    },
    methods: {
      login() {
        let obj = {
          "imgCode": "",
          "imgKey": "",
          "mobile": this.value,
          "smsCode": this.yzvalue
        }
        if (this.verify()) {
          this.axios_(obj, {
            'X-Host': 'mall.user.sms-code-login'
          })
            .then(value => {
              if (value.data.status === 0) {
                localStorage.setItem("token", value.data.data.token)
                this.$store.commit("user", value.data.data)
                this.$router.push("/main/mine")
              }
            })
        }
      },
      sign(text) {
        Dialog.confirm({
          message: text
        })
          .then(() => {
            // this.$router.push('/main/city')
          })
          .catch(() => {

          });
      },
      verify() {
        if (!(/^[0-9]{11}$/).test(this.value) && this.value !== "") {
          this.isShow1.bool = true
          this.isShow1.text = "请输入11位正确的手机号"
        } else if (this.value === '') {
          this.isShow1.bool = true
          this.isShow1.text = "请输入手机号"
        } else {
          return true
        }
      },
      verifyNumber() {
        let obj = {
          "imgCode": "",
          "imgKey": "",
          "mobile": this.value,
          "type": "1"
        }
        if (this.verify() && this.verifyStr === "获取验证码") {
          this.axios_(obj, {
            'X-Host': 'mall.user.sms-code.send'
          })
            .then(value => console.log(value.data))
          let a = 60
          this.timer[0] = setInterval(() => {
            this.verifyStr = a + "秒"
            a--
            if (a < 0) {
              this.verifyStr = "获取验证码"
              clearInterval(this.timer[0])
            }
          }, 1000)
        }
      },
      axios_(obj, host) {
        return axios.post("https://m.maizuo.com/gateway", {
          ...obj
        }, {
          headers: {
            'Content-Type': 'application/json;charset=UTF-8',
            'X-Client-Info': '{"a":"3000","ch":"1002","v":"1.0.0","e":"16357749354350673021829121"}',
            ...host
          },
        })
      }
    },
    watch: {
      value() {
        this.isBool = (/^[0-9]{11}$/).test(this.value)
      }
    }
  }
</script>
<style>
  html, body {
    height: 100%;
  }
</style>
<style scoped lang="scss">


  .content_login {
    height: 100%;
    background: #fff;

    .header {
      padding-top: 79px;
      margin: 0 auto 40px;
      text-align: center;
      height: 60px;
      line-height: 60px;

      img {
        height: 60px;
      }
    }

    .body {
      margin: 0 25px;

      .iconfont {
        position: absolute;
        color: rgb(189, 192, 197);
        right: 90px;
        top: 20px;
      }

      div:nth-of-type(1) {
        position: relative;

        div {
          text-align: left;
          color: #ff5f16;
          width: 100%;
          line-height: 11px;
          font-size: 11px;
          margin-top: 7px;
        }

        button {
          position: absolute;
          right: 0px;
          top: 19px;
          font-size: 13px;
          border: 0;
          background: inherit;
          color: #bdc0c5;
        }
      }

      input {
        border: 0;
        border-bottom: 1px solid;
        height: 15px;
        line-height: 15px;
        padding: 20px 0;
        width: 100%;
        font-size: 15px;
        color: #191a1b;
        outline-width: 0;
        border-bottom: 1px solid #ededed;
      }
    }

    .footer {
      width: calc(100% - 50px);
      line-height: 45px;
      font-size: 16px;
      margin: 70px 25px 0;
      border-radius: 3px;
      text-align: center;
      background-color: #ff5f16;
      height: 44px;
      color: rgba(255, 255, 255, 0.3);
      border: none;
    }

    .footer_ {
      width: calc(100% - 50px);
      line-height: 45px;
      font-size: 16px;
      margin: 70px 25px 0;
      border-radius: 3px;
      text-align: center;
      background-color: #ff5f16;
      height: 44px;
      color: rgba(255, 255, 255, 1);
      border: none;
    }
  }
</style>
